// BASIC
import React, { Component } from 'react'
import styled, {css, keyframes} from 'styled-components'
import {Link} from 'react-router-dom'
// ICONS
import {MdExitToApp} from 'react-icons/md'
import {FaGamepad} from 'react-icons/fa'
import {GiTwoCoins} from 'react-icons/gi'
import {TiChartLine} from 'react-icons/ti'

const fade = keyframes`
	from {
		opacity: 0;
		transform: scale(0.9);
	}
	to {
		opacity: 1;
		transform: scale(1);
	}
`
const ShopComponent = styled.div`
	display: none;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	flex-direction: column;
	padding: 0 50px;
	align-items: center;
	background-color: var(--color-dark);
	z-index: 5;
	font-size: 20px;
	color: var(--color-primary);
	animation: ${fade} 0.3s ease;
	${props =>
		props.show &&
		css`
			display: flex;
		`
	};
`
const ShopTitle = styled.h1`
	font-size: 30px;
	margin: 20px 0;
`
const Info = styled.div`
	display: flex;
	width: 100%;
	justify-content: space-between;
	margin-bottom: 50px;
`
const Coins = styled.div``
const Text = styled.span`
display: none;
	@media (min-width: 500px) {
		display: inline;
	}
`
const CoinIcon = styled(GiTwoCoins)`
	transform: translateY(3px);
`
const Experience = styled.div``
const ExperienceIcon = styled(TiChartLine)`
	transform: translateY(3px);
`
const Number = styled.strong`
	color: var(--color-decorative);
`
const Products = styled.div`
	width: 90vw;
	max-width: 1000px;
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	height: calc(100vh - 250px);
	overflow-y: scroll;
	/* background-color: gray; */
	::-webkit-scrollbar {
		width: 0;
	}
`
const Product = styled.div`
	min-width: 150px;
	height: 150px;
	margin: 10px;
	border: 2px solid var(--color-secondary);
	border-radius: 20px;
	background-color: var(--color-bg);
`
const Exit = styled.div`
	position: absolute;
	bottom: 30px;
	width: 100%;
	display: flex;
	justify-content: space-evenly;
`
const Icon = styled.div`
	font-size: 40px;
	color: var(--color-decorative);
	transition: 0.3s ease;
	:hover {
		cursor: pointer;
		opacity: 0.5;
		transform: scale(0.99);
	}
`

class Shop extends Component {
	render() {
		return (
			<ShopComponent show={this.props.show}>
				<ShopTitle>EasyShop</ShopTitle>
				<Info>
					<Coins>
						<Text>EasyCoins:</Text> <Number>{this.props.coins} <CoinIcon /></Number>
					</Coins>
					<Experience>
						<Text>Doświadczenie:</Text> <Number>{this.props.experience} <ExperienceIcon /></Number>
					</Experience>
				</Info>
				<Products>
					<Product />
					<Product />
					<Product />
					<Product />
					<Product />
				</Products>
				<Exit>
					<Link to='/'>
						<Icon>
							<FaGamepad />
						</Icon>
					</Link>
					<Icon>
						<MdExitToApp onClick={this.props.onBack} />
					</Icon>
				</Exit>
			</ShopComponent>
		);
	}
}

export default Shop